<script>
import TableOptions from "@/components/TableOptions.vue"
export default {
  props: [
    'operations', 'showOptions', 'showTable'
  ],
  components: {
    TableOptions
  },
  data: () => ({
    types: ['all','replenish','dividend'],
    type: 'all'
  }),
  methods: {
      selectOption(idx) {
        this.type = this.types[idx]
        this.$emit('changeStatus', this.type)
      },
      getClass(idx) {
        return ((idx % 2 == 0)? 'dark': 'light')
      }
  },
}
